<template>
  <el-time-select
    @change="setTime"
    v-model="value"
    :picker-options="{
      start: '08:30',
      step: '00:15',
      end: '18:30',
    }"
    placeholder="Select time"
  >
  </el-time-select>
</template>

<script>
export default {
  data() {
    return {
      value: "",
    };
  },
  methods: {
    setTime() {
      this.$emit("setTime", this.value);
    },
  },
};
</script>

