<template>
  <ul class="toy-list card-grid">
    <li v-for="toy in toys" :key="toy._id" class="toy-preview-container card-toy" >
      <toy-preview class="toy-tag"  :toy="toy" />
      <div class="btns-container">
        <button @click="remove(toy._id)">X</button>
        <router-link :to="'/details/' + toy._id">Details</router-link>
        <router-link :to="'/edit/' + toy._id">Edit</router-link>
      </div>
    </li>
  </ul>
</template>


<script>
import toyPreview from "@/cmps/toy-preview.vue";
import elementCard from "@/cmps/element-card";
import ElementCard from './element-card.vue';

export default {
  name: "toy-list",
  props: {
    toys: Array,
  },
  methods: {
    remove(toyId) {
      this.$emit("remove", toyId);
    },
  },
  components: {
    toyPreview,
    elementCard
  },
};
</script>
