<template>
  <div class="chat">
    <button @click="closeChat">X</button>
    <span class="title-chat">welcome to toy chat</span>

    <ul class="line-chat">
      <li v-for="(message, index) in messages" :key="index">
        <span>{{ message }}</span>
      </li>
    </ul>

    <div class="user-chat">
      <input type="text" v-model="newMsg" />
      <button @click="pushMsg">Send</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "chat",
  data() {
    return {
      newMsg: "",
      messages: ["hello you can ask us anything"],
      supportMsg: "Support: Sure things honey",
    };
  },
  methods: {
    pushMsg() {
      this.messages.push(this.newMsg);
      this.newMsg = "";
      setTimeout(() => {
        this.messages.push(this.supportMsg);
      }, 2000);
    },
    escapekeylistener(ev) {
      if (ev.code === "Enter") {
        console.log("push msh");
        //   this.closeChat();
        this.pushMsg();
      }
    },
    closeChat() {
      this.$emit("closeChat");
    },
  },
  created() {
    document.addEventListener("keyup", this.escapekeylistener);
  },
};
</script>



