<template>
  <section class="img-upload-container">
    <label v-if="!isLoading" for="imgUploader">
      <img
        class="upload"
        src="http://www.pngmart.com/files/3/Upload-Button-Transparent-PNG.png"
        alt=""
      />
    </label>

    <img
      class="loading"
      v-else
      src="https://i.pinimg.com/originals/f6/65/6a/f6656aa6fdb6b8f905dea0bcc2d71dd8.gif"
      alt=""
    />
    <input type="file" id="imgUploader" @change="onUploadImg" />
  </section>
</template>

<script>
import { uploadImg } from "@/services/img-upload.service.js";
export default {
  data() {
    return {
      isLoading: false,
    };
  },

  methods: {
    async onUploadImg(ev) {
      this.isLoading = true;
      const res = await uploadImg(ev);
      this.$emit("save", res.url);
      console.log("onUploadImg -> res", res);
      this.isLoading = false;
    },
  },
};
</script>

<style>
input {
  display: none;
}

.loading {
  height: 200px;
}

img.upload {
  height: 100px;
  cursor: pointer;
}
</style>