import { httpService } from "./http.service.js";
import { utilService } from "./util.service.js";

var gLoggedinUser = utilService.loadFromStorage('loggedinUser');

const USER_URL = (process.env.NODE_ENV !== 'development')
? '/api/user/'
: '//localhost:3030/api/user/';

export const userService = {
      query,
      getById,
      save,
      remove,
      login,
      logout,
      signup,
}

// /* CRUD */

function query() {
      return httpService.get('user')
}

function getById(userId) {
      return httpService.get('user/' + userId)
}

function save(user) {
      if (user._id) {
            return httpService.put('user/' + user._id, user)
      } else {
            return httpService.post('user/', user)
      }
}

function remove(userId) {
      return httpService.delete('user/' + userId)
}

// // /* LOGIN */

function login(credentials) {
     return httpService.post('auth/login', credentials)
      .then((user) => {
            console.log('the user got in front is:', user)
            gLoggedinUser=user
            utilService.saveToStorage('loggedinUser', user)
            return user
      })
}

function logout() {
      return httpService.post('auth/logout')
      .then(() => {
            gLoggedinUser=null
            utilService.saveToStorage('loggedinUser', null)
      })
}

function signup(signupCredentials) {
      return axios.post('api/user/' + 'signup', signupCredentials).then(res => res.data)
            .then(user => _handleLoggedinUser(user))
}




