<template>
  <div class="toy-filter">
    <input
      class="txt-search"
      type="text"
      @input="setFilter"
      placeholder="Search...."
      v-model="filterBy.name"
    />

    <div>
      <label> in stock filter: </label>
      <element-select
        :option="inStockFilterOption"
        :eventType="'setInStockFilter'"
        @setInStockFilter="setInStockFilter"
      >
      </element-select>
    </div>

    <div>
      <label> type filter: </label>
      <element-select
        :option="typeFilterOption"
        :eventType="'setTypeStockFilter'"
        @setTypeStockFilter="setTypeStockFilter"
      >
      </element-select>

      <button @click="sort">sort</button>
    </div>
  </div>
</template>

<script>
import elementSelect from "@/cmps/element-select.vue";

export default {
  name: "toy-filter",
  props: {
    msg: String,
  },
  data() {
    return {
      filterBy: {
        name: "",
        stockFilter: "all",
        typeFilter: "all",
        sortBy: "name",
      },
      inStockFilterOption: [
        {
          value: "all",
          label: "All",
        },
        {
          value: "true",
          label: "in stock",
        },
        {
          value: "false",
          label: "not in stock",
        },
      ],
      typeFilterOption: [
        {
          value: "all",
          label: "All",
        },
        {
          value: "Educational",
          label: "Educational",
        },
        {
          value: "Funny",
          label: "Funny",
        },
        {
          value: "Adult",
          label: "Adult",
        },
      ],
    };
  },
  methods: {
    setFilter() {
      this.$emit("filtered", this.filterBy);
    },
    sort() {
      if (this.filterBy.sortBy === "name") {
        this.filterBy.sortBy = "price";
      } else {
        this.filterBy.sortBy = "name";
      }
      this.$emit("filtered", this.filterBy);
    },
    setInStockFilter(inStockValue) {
      this.filterBy.stockFilter = inStockValue;
      this.setFilter();
    },
    setTypeStockFilter(typeValue) {
      this.filterBy.typeFilter = typeValue;
      this.setFilter();
    },
  },
  components: {
    elementSelect,
  },
};
</script>
