<template>
  <main>
    <GmapMap
      ref="mapRef"
      :center="{ lat: 32.15979, lng: 34.84447 }"
      :zoom="12"
      map-type-id="terrain"
      style="width: 500px; height: 300px"
    >
      <GmapMarker
         v-for="(m, index) in markers"
        :key="index"
        :position="m.position"
        :clickable="true"
        :draggable="true"
        @click="center = m.position"
      />
    </GmapMap>
    <section class="btn-google-container">
      <button @click="goTo({ lat: 32.173961, lng: 34.804901 })">
        herzeliya
      </button>
      <button @click="goTo({ lat: 31.899474, lng: 35.010975 })">
        Macabim reut
      </button>
      <button @click="goTo({ lat: 32.182956, lng: 34.872406 })">raanana</button>
    </section>
  </main>
</template>

<script>
export default {
  data() {
    return {
      markers: [
        { position: { lat: 32.173961, lng: 34.804901 } },
        { position: { lat: 31.899474, lng: 35.010975 } },
        { position: { lat: 32.182956, lng: 34.872406 } },
      ],
    };
  },
  methods: {
    goTo(position) {
      this.$refs.mapRef.$mapPromise.then((map) => {
        map.panTo(position);
      });
    },
  },
};
</script>

