import { toyService } from '../../services/toy.service.js'


export default ({
      state: {
            toys: [],
            filterBy: {
                  name: '',
                  stockFilter: 'all',
                  typeFilter: 'all',
                  sortBy: "name"
            },
      },
      getters: {
            toysForDisplay(state) {
                  return state.toys
                  // //filter name, type and stock
                  // const regex = new RegExp(state.filterBy.name, 'i')
                  // var toysForDisplay = state.toys.filter(toy => {
                  //       return regex.test(toy.name) && toy.type === state.filterBy.typeFilter || state.filterBy.typeFilter === 'all'
                  //             && JSON.stringify(toy.inStock) === state.filterBy.stockFilter || state.filterBy.stockFilter === 'all'
                  // })
                  // //handle sort by name / price
                  // if (state.filterBy.sortBy === 'name') {
                  //       utilService.sortByName(toysForDisplay)
                  // } else {
                  //       utilService.sortByPrice(toysForDisplay)
                  // }
                  // return toysForDisplay
            },
            toysCountByType(state) {
                  let adults = state.toys.filter(toy => toy.type === 'Adult');
                  let adoltPriceAvg = adults.reduce((acc, value) => {
                        return acc + value.price
                  }, 0)

                  const funny = state.toys.filter(toy => toy.type === 'Funny');
                  let funnyPriceAvg = funny.reduce((acc, value) => {
                        return acc + value.price
                  }, 0)
                  const education = state.toys.filter(toy => toy.type === 'Educational');
                  let educationPriceAvg = education.reduce((acc, value) => {
                        return acc + value.price
                  }, 0)

                  return [adoltPriceAvg / adults.length, funnyPriceAvg / funny.length, educationPriceAvg / education.length]

            },
            toysInStockByType(state) {
                  let adults = state.toys.filter(toy => toy.type === 'Adult' && toy.inStock === true);
                  const funny = state.toys.filter(toy => toy.type === 'Funny' && toy.inStock === true);
                  const education = state.toys.filter(toy => toy.type === 'Educational' && toy.inStock === true);
                  return [adults.length, funny.length, education.length]
            }
      },
      mutations: {
            setToys(state, { toys }) {
                  state.toys = toys
            },
            addToy(state, { addedToy }) {
                  state.toys.push(addedToy);
            },
            removeToy(state, { toyId }) {
                  const idx = state.toys.findIndex(t => t._id === toyId)
                  state.toys.splice(idx, 1)
            },
            updateToy(state, { updatedToy }) {
                  const idx = state.toys.findIndex(t => t._id === updatedToy._id)
                  state.toys.splice(idx, 1, updatedToy)
            },
            setFilter(state, { filterBy }) {
                  state.filterBy = filterBy
            },
      },
      actions: {
            loadToys(context) {
                  toyService.query(context.state.filterBy)
                        .then(toys => {
                              context.commit({ type: 'setToys', toys })
                        })
            },
            addToy({ commit }, { newtoy }) {
                  return toyService.save(newtoy)
                        .then((addedToy) => {
                              commit({ type: 'addToy', addedToy })
                        })
            },
            removeToy({ commit }, { toyId }) {
                  return toyService.remove(toyId)
                        .then(() => {
                              commit({ type: 'removeToy', toyId })
                        })
            },
            updateToy({ commit }, { toy }) {
                  return toyService.save(toy)
                        .then((updatedToy) => {
                              commit({ type: 'updateToy', updatedToy })
                        })
            },
            setFilter({ commit }, { filterBy }) {
                  toyService.query(filterBy)
                        .then(toys => {
                              commit({ type: 'setToys', toys })
                        })
            },

      },
})
