<template>
  <el-select @change="setInStockFilter" v-model="value" placeholder="In Stock">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    >
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: 'element-select',
  props: ['option', 'eventType'],
  data() {
    return {
      options: this.option,
      value: "all",
    };
  },
  methods: {
    setInStockFilter() {
      this.$emit(this.eventType, this.value )
    },
  },
};
</script>