<template>
  <div v-if="toyToAdd" class="toy-add">
    <h3>Add toy:</h3>
    <form class="add-toy-form" @submit.prevent="add">
      <div>
        <label for="name">name:</label>
        <input type="text" v-model="toyToAdd.name" />
      </div>

      <div>
        <label for="at">in stock:</label>
        yes<input type="radio" :value="true" v-model="toyToAdd.inStock" />
        no<input type="radio" :value="false" v-model="toyToAdd.inStock" />
      </div>

      <div>
        <label for="type">type:</label>
        <select name="type" id="type" v-model="toyToAdd.type">
          <option value="Educational">Educational</option>
          <option value="Funny">Funny</option>
          <option value="Adult">Adult</option>
        </select>
      </div>

      <div>
        <label for="price">price:</label>
        <input type="text" v-model.number="toyToAdd.price" />
        <button>Add</button>
      </div>
    </form>
    <router-link class="back-btn" to="/toy">Back</router-link>
  </div>
</template>

<script>
import { toyService } from "../services/toy.service.js";

export default {
  name: "toy-add",
  data() {
    return {
      toyToAdd: toyService.getEmptyToy(),
    };
  },
  methods: {
    add() {
      // this.toyToAdd.createdAt = Date.now();
      const toyCopy = JSON.parse(JSON.stringify(this.toyToAdd));
      this.$store
        .dispatch({ type: "addToy", newtoy: toyCopy })
        .then(() => {
          // showMsg("toy added successfully");
        })
        .catch((err) => {
          // showMsg("error with added toy", "danger");
        })
        .finally(() => {
          this.$router.push("/toy");
          this.toyToAdd = toyService.getEmptyToy();
        });
    },
  },
};
</script>
