

<template>
  <div class="toy-app">
    <pop-up>
      <div slot="header"></div>
      <div slot="main" v-if="isChatOpen">
        <chat @closeChat="closeChat"></chat>
      </div>
      <div slot="footer"></div>
    </pop-up>

    <section class="flex-center">
      <h1>Toy app page</h1>
      <button v-if="!isChatOpen" class="open-chat" @click="openChat">
        Open Chat
      </button>
    </section>

    <toy-filter @filtered="setFiltered"></toy-filter>
    <button @click="moveToAddToy">Add Toy</button>
    <toy-list :toys="toysForDisplay" @remove="removeToy"></toy-list>
  </div>
</template>




<script>
// @ is an alias to /src
import toyFilter from "@/cmps/toy-filter.vue";
import toyList from "@/cmps/toy-list.vue";
import popUp from "@/cmps/pop-up.vue";
import chat from "@/cmps/chat.vue";

export default {
  name: "Toy-app",
  data() {
    return {
      isChatOpen: false,
    };
  },
  methods: {
    moveToAddToy() {
      this.$router.push("/toy-add");
    },
    removeToy(toyId) {
      this.$store
        .dispatch({ type: "removeToy", toyId })
        .then(() => {
          // showMsg("toy remove successfully");
          this.$store.dispatch({ type: "loadToys" });
        })
        .catch((err) => {
          // showMsg("error with remove toy", "danger");
        });
    },
    setFiltered(filter) {
      this.$store.commit({
        type: "setFilter",
        filterBy: JSON.parse(JSON.stringify(filter)),
      });

      this.$store.dispatch({
        type: "setFilter",
        filterBy: JSON.parse(JSON.stringify(filter)),
      });
    },
    openChat() {
      this.isChatOpen = true;
    },
    closeChat() {
      this.isChatOpen = false;
    },
    escapekeylistener(ev) {
      if (ev.code === "Escape") {
        console.log('close modal')
        this.closeChat()
      }
    },
  },
  computed: {
    toysForDisplay() {
      return this.$store.getters.toysForDisplay;
    },
  },
  created() {
    console.log("Toy app created!!!");
    this.$store.dispatch({ type: "loadToys" });
    document.addEventListener("keyup", this.escapekeylistener);
  },
  components: {
    toyFilter,
    toyList,
    popUp,
    chat,
  },
};
</script>




