import { utilService } from "./util.service.js";
import { storageService } from "./async-storage.service.js";
import { httpService } from "./http.service.js";


const KEY = 'toysDB'

// const TOY_URL = 'http://localhost:3030/api/toy/'
const TOY_URL = (process.env.NODE_ENV !== 'development')
      ? '/api/toy/'
      : '//localhost:3030/api/toy/';

// _createToys();

export const toyService = {
      query,
      getById,
      remove,
      save,
      getEmptyToy,
      addReview
}

function query(FilterBy='') {
      return httpService.get('toy', FilterBy, true)
      // return axios.get('localhost:3030/api/toy/', { params: FilterBy })
      //       .then(res => res.data)
      // return storageService.query(KEY);
      // localStorage.setItem(KEY, JSON.stringify(toys))
}

function getById(id) {
      return httpService.get('toy/' + id)
}

function remove(id) {
      return httpService.delete('toy/' + id)
}

function save(toy) {
      if (toy._id) {
            return httpService.put('toy/' + toy._id, toy)
      } else {
            return httpService.post('toy/', toy)
      }
}


function addReview(toyId, review) {
      return httpService.post('toy/' + toyId + '/review', review)
}

function getEmptyToy() {
      return {
            name: '',
            price: null,
            type: '',
            inStock: null,
            review: [],
      }
}



