<template>
  <main class="dashbord">
    <section class="chart-price">
      <h1>avg price of toy type</h1>
      <chart v-if="toysAvgByType" :data="toysDataForDisplay" />
    </section>
    <section class="chart-price">
      <h1>percent of toys in stock by type</h1>
      <chart v-if="toysInStockByType" :data="toysInStockForDisplay" />
    </section>
    <section class="user-pref">
      <h1>Choose Your color</h1>
      <element-color @setColor="setColor"></element-color>
      <h1>Choose Your Time</h1>
      <element-time @setTime="setTime"></element-time>
    </section>

    <section>
      <h1>upload photo</h1>
      <img-uploader @save="saveImg"></img-uploader>
    </section>
  </main>
</template>

<script>
import chart from "@/cmps/chart";
import elementColor from "@/cmps/element-color";
import elementTime from "@/cmps/element-time";
import imgUploader from "@/cmps/img-upload";

export default {
  name: "dashboard-component",
  data() {
    return {
      toysAvgByType: null,
      toysInStockByType: null,
      userColor: null,
      userTime: null,
      imgUrls: [],
    };
  },
  methods: {
    setColor(colorValue) {
      console.log("got", colorValue);
      this.userColor = colorValue;
    },
    setTime(timeValue) {
      console.log("got", timeValue);
      this.userTime = timeValue;
    },
    saveImg(imgUrl) {
      this.imgUrls.push(imgUrl);
    },
  },
  computed: {
    toysDataForDisplay() {
      return {
        datasets: [
          {
            backgroundColor: ["magenta", "green", "purple"],
            data: this.toysAvgByType,
          },
        ],
        // These labels appear in the legend and in the tooltips when hovering different arcs
        labels: ["Adolt", "Educational", "Funny"],
      };
    },
    toysInStockForDisplay() {
      return {
        datasets: [
          {
            backgroundColor: ["yellow", "red", "blue"],

            data: this.toysInStockByType,
          },
        ],
        // These labels appear in the legend and in the tooltips when hovering different arcs
        labels: ["Adolt", "Educational", "Funny"],
      };
    },
  },
  created() {
    this.toysAvgByType = this.$store.getters.toysCountByType;
    this.toysInStockByType = this.$store.getters.toysInStockByType;
  },
  components: {
    chart,
    elementColor,
    elementTime,
    imgUploader,
  },
};
</script>

