import { httpService } from './http.service'
import { storageService } from './async-storage.service'
import { userService } from './user.service'

export const reviewService = {
      add,
      query,
      remove
}

function query(filterBy) {
      return httpService.get('review', filterBy, true)

      // return httpService.get(queryStr)
      // console.log('in service we got filter:', filterBy)
      // var queryStr = (!filterBy) ? '' : `?toyId=${filterBy}`
      // `review${queryStr}`
      // return storageService.query('review')
}

function remove(reviewId) {
      return httpService.delete(`review/${reviewId}`)
      // return storageService.delete('review', reviewId)
}


async function add(review) {
      const addedReview = await httpService.post(`review`, review)
      return addedReview
      // review.byUser = userService.getLoggedinUser()
      // review.aboutUser = await userService.getById(review.aboutUserId)
      // const addedReview = storageService.post('review', review)
}


