<template>
  <div v-if="toyToEdit" class="toy-edit">
    <h1>welcome to Toy edit page</h1>

    <form @submit.prevent="update">
      <label for="name">name:</label>
      <input type="text" v-model="toyToEdit.name" />

      <label for="at">created at:</label>
      <input type="text" v-model="toyToEdit.createdAt" />

      <label for="stock">in stock:</label>
      yes<input type="radio" :value="true" v-model="toyToEdit.inStock" />
      no<input type="radio" :value="false" v-model="toyToEdit.inStock" />

      <label for="type">type:</label>
      <select name="type" id="type" v-model="toyToEdit.type">
        <option value="Educational">Educational</option>
        <option value="Funny">Funny</option>
        <option value="Adult">Adult</option>
      </select>

      <label for="price">price:</label>
      <input type="text" v-model="toyToEdit.price" />

      <button>submit</button>
    </form>
    <router-link to="/toy">Back</router-link>
  </div>
</template>

<script>
// @ is an alias to /src
import { toyService } from "../services/toy.service.js";

export default {
  name: "toy-edit",
  data() {
    return {
      toyToEdit: null,
    };
  },
  methods: {
    loadToy() {
      const id = this.$route.params.toyId;
      toyService.getById(id).then((toy) => {
        console.log('toy gotted in toy eddit is:',toy);
        this.toyToEdit = JSON.parse(JSON.stringify(toy));
      });
    },
    update() {
      console.log("Updating...", this.toyToEdit);
      const toyCopy = JSON.parse(JSON.stringify(this.toyToEdit));
      this.$store
        .dispatch({ type: "updateToy", toy: toyCopy })
        .then(() => {
          // showMsg("toy update successfully");
          this.$router.push("/toy");
        })
        .catch((err) => {
          // showMsg("error with update toy", "danger");
        });
    },
  },
  created() {
    this.loadToy();
  },
  watch: {
    "$route.params.toyId"(id) {
      console.log("Changed to", id);
      this.loadToy();
    },
  },
  components: {},
};
</script>

