<template>
  <div v-if="user" class="user-details">
    <section class="user-data-show">
      <h1>welcome to user details page</h1>
      <h3>{{ userToShow._id }}</h3>
      <h3>{{ userToShow.username }}</h3>
      <h3>{{ userToShow.fullname }}</h3>

      <router-link to="/admin">Back</router-link>
    </section>

<h4>Review list:</h4>
    <section>
      <ul class="review-list">
        <li v-for="review in reviewsToShow" :key="review._id">
          <h3>{{ review.content }} By: {{ review.user.fullname }}</h3>

        </li>
      </ul>
    </section>
  </div>
</template>


<script>
// @ is an alias to /src
import { userService } from "../services/user.service.js";
import { reviewService } from "../services/review.service.js";

export default {
  name: "user-details",
  data() {
    return {
      user: null,
      review: {
        txt: "",
      },
      reviews: [],
    };
  },
  computed: {
    userToShow() {
      return this.user;
    },
    reviewsToShow() {
      return this.reviews;
    },
  },
  methods: {
    async loaduser() {
      console.log("try get user");
      const id = this.$route.params.userId;
      await userService.getById(id).then((user) => (this.user = user));
      let reviews = await reviewService.query({userId: id});
      console.log("we got load review in details comp", reviews);
      this.reviews = reviews;
    },
    async addReview() {
      await reviewService.add({
        userId: this.user._id,
        content: this.review.txt,
      });
      await this.$store.dispatch({ type: "loadusers" });
      await this.loaduser();
      this.review = { txt: "" };
    },
  },
  watch: {
    "$route.params.userId"(id) {
      console.log("Changed to", id);
      this.loaduser();
    },
  },
  created() {
    this.loaduser();
  },
};
</script>

