import { reviewService } from '../../services/review.service.js'


export default ({
      state: {
            reviews: [],
      },
      getters: {
            reviewsForDisplay(state) {
                  return state.reviews
            },
      },
      mutations: {
            setReviews(state, { reviews }) {
                  state.reviews = reviews
            },
            addReview(state, { addedReview }) {
                  state.reviews.push(addedReview);
            },
            removeReview(state, { reviewId }) {
                  const idx = state.reviews.findIndex(t => t._id === reviewId)
                  state.reviews.splice(idx, 1)
            },
      },
      actions: {
            loadReviews({ commit }) {
                  reviewService.query()
                        .then(reviews => {
                              commit({ type: 'setReviews', reviews })
                        })
            },
            addReview({ commit }, { newReview }) {
                  return reviewService.add(newReview)
                        .then((addedReview) => {
                              commit({ type: 'addReview', addedReview })
                        })
            },
            removeReview({ commit }, { reviewId }) {
                  return reviewService.remove(reviewId)
                        .then(() => {
                              commit({ type: 'removeReview', reviewId })
                        })
            },
      },
})
