<template>
  <section>
    <h1>welcome to review page:</h1>
    <ul class="review-list card-grid">
      <li
        v-for="review in reviews"
        :key="review._id"
        class="toy-preview-container card-toy"
      >
        <h1>review content: {{ review.content }}</h1>
        <h1>about toy: {{ review.toy.name }}</h1>
        <h1>review from: {{ review.user.fullname }}</h1>
        <!-- <router-link :to="'/review/' + review._id">Details</router-link> -->

        <!-- <div class="btns-container">
          <button @click="remove(review._id)">X</button>
        </div> -->
      </li>
    </ul>
  </section>
</template>

<script>
import { reviewService } from "../services/review.service.js";
export default {
  data() {
    return {
      reviews: [],
    };
  },
  computed: {
    reviewsToShow() {
      return this.reviews;
    },
  },
  methods: {
    async loadReviews() {
      let reviews = await reviewService.query();
      console.log("we got load review in details comp", reviews);
      this.reviews = reviews;
    },
  },
  created() {
    this.loadReviews();
  },
};
</script>

